
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

export default {
data() {
return {
posterList: [],
personalized: [],
privatecontent: [],
newsong: []
};
},
components: {
Swiper,
Flexbox,
FlexboxItem,
SwiperItem
},
computed: {},
methods: {
getData() {
this.$http
.all([
this.$musicApi.getBanner(this),
this.$musicApi.getPersonalized(this, { limit: 6 })
])
.then(
this.$http.spread(function(res1, res2) {

this.posterList = res1.data.banners;
this.personalized = res2.data.result;
}.bind(this))
)
.catch(err => {
console.log('');
this.posterList = this.$localData.posterList.banners;
this.personalized = this.$localData.personalized.result.slice(0, 6);
});
this.$http
.all([
this.$musicApi.getPrivatecontent(this),
this.$musicApi.getNewsong(this, { limit: 10 })
])
.then(
this.$http.spread(function(res1, res2) {

this.privatecontent = res1.data.result;
this.newsong = res2.data.result;
}.bind(this))
)
.catch(err => {
this.privatecontent = this.$localData.privatecontent.result;
this.newsong = this.$localData.newsong.result.slice(0, 10);
});
}
},
mounted: function() {
this.$nextTick(function() {
this.getData();
});
}
};



